import {profileAPI} from "../api/api";
import {stopSubmit} from "redux-form";

const ADD_POST = 'ADD_POST';
const SET_USER_PROFILE = 'SET_USER_PROFILE';
const SET_USER_STATUS = 'SET_USER_STATUS';
const DELETE_POST = 'DELETE_POST';
const SAVE_PHOTO_SUCCESS = 'SAVE_PHOTO_SUCCESS';

//ACTION CREATORS
export const addPost = postText => ({type: ADD_POST, postText});
export const setUserProfile = profile => ({type: SET_USER_PROFILE, profile});
export const setUserStatus = status => ({type: SET_USER_STATUS, status});
export const deletePost = postId => ({type: DELETE_POST, postId});
export const savePhotoSuccess = photos => ({type: SAVE_PHOTO_SUCCESS, photos});

let initialState = {
    posts: [
        {id: "1", message: "Seydoux", likes: 7},
        {id: "2", message: "Reedus", likes: 4},
        {id: "3", message: "Mikkelson", likes: 6},
    ],
    profile: null,
    status: ""
};

const profileReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_POST:
            let newPost = {
                id: "4",
                message: action.postText,
                likes: 0
            };
            return {
                ...state,
                posts: [...state.posts, newPost]
            };

        case SET_USER_PROFILE:
            return {
                ...state,
                profile: action.profile
            };
        case SET_USER_STATUS:
            return {
                ...state,
                status: action.status //статус, который приходит с сервера
            };
        case DELETE_POST:
            return {
                ...state,
                posts: state.posts.filter(p => p.id !== action.postId)
            };
        case SAVE_PHOTO_SUCCESS:
            return {
              ...state,
              profile: {...state.profile, photos: action.photos}
            };
        default: //если придёт незафиксир action то просто вернётся первонач state
            return state;
    }
};

//THUNK
export const getProfile = userId => async dispatch => {
    const data = await profileAPI.getProfile(userId);
    dispatch(setUserProfile(data));
};


export const getStatus = userId => async dispatch => {
    const response = await profileAPI.getStatus(userId);
    dispatch(setUserStatus(response.data));
};

export const updateStatus = status => async dispatch => {
    try{
        const response = await profileAPI.updateStatus(status);
        if (response.data.resultCode === 0) {
            dispatch(setUserStatus(status));
        }
    } catch (e) {
        //сдесь будут отображаться перехваченные ошибки, которые можно будет диспатчить и отображать
    }

};

export const savePhoto = file => async dispatch => {
    const response = await profileAPI.savePhoto(file);
    if(response.data.resultCode === 0) {
        dispatch(savePhotoSuccess(response.data.data.photos));
    }
};

export const saveProfile = profile => async (dispatch, getState) => {
    const userId = getState().auth.userId;
    const response = await profileAPI.saveProfile(profile);
    if(response.data.resultCode === 0) {
        dispatch(getProfile(userId));
    } else {
        let message = response.data.messages.length > 0 ? response.data.messages[0] : "some error";
        dispatch(stopSubmit("profileDesc", {_error: message}));
        return Promise.reject(response.data.messages[0])
        //dispatch(stopSubmit("profileDesc", {"contacts": {"facebook": message}}));

        //надо посмотреть это!!!!!!!!!!!!
        // let key = data.messages[0].match(/Contacts->(\w+)/)[1].toLowerCase();
        // dispatch(stopSubmit('editProfile', {
        //     contacts: {[key]: data.messages[0]},
        // }));
    }
};

export default profileReducer