import React, {Component} from 'react';
import './App.css';
import Navbar from "./components/Navbar/Navbar";
import News from "./components/News/News";
import Music from "./components/Music/Music";
import Gallery from "./components/Gallery/Gallery";
import Settings from "./components/Settings/Settings";

import {BrowserRouter, HashRouter, Redirect, Route, Switch, withRouter} from "react-router-dom";
import UsersContainer from "./components/Users/UsersContainer";
import HeaderContainer from "./components/Header/HeaderContainer";
import Login from "./components/Login/Login";

// import DialogsContainer from "./components/Dialogs/DialogsContainer";
// import ProfileContainer from "./components/Profile/ProfileContainer";


import {connect, Provider} from "react-redux";
import {compose} from "redux";
import {initApp} from "./redux/appReducer";
import Preloader from "./components/common/Preloader/Preloader";
import store from "./redux/redux_store";
import {withSuspense} from "./hoc/withSuspense";

const DialogsContainer = React.lazy(() => import('./components/Dialogs/DialogsContainer'));
const ProfileContainer = React.lazy(() => import('./components/Profile/ProfileContainer'));

class App extends Component {
    componentDidMount() {
        this.props.initApp();
    }

    render() {
        let Background = "https://i.playground.ru/p/SCfJ19mum0SsgDhAN1jALA.jpeg";
        if (!this.props.init) {
            return <Preloader/>
        }
        return (
            <div className='app'>
                <HeaderContainer/>
                <Navbar/>
                <div className='app-wrap_content'>
                    <div className="bg_img" style={{backgroundImage: `url(${Background})`}}/>
                    {/*switch почему-то не работает, работают только те, что обёрнуты Suspense, остальные выдаёт You need to enable JavaScript to run this app*/}
                    <Switch>
                        {/*можно сделать так, оборачивать HOC-ом каждую компоненту
                    <Route path='/profile/:userId?' render={() => withSuspense(<ProfileContainer/>)}/>*/}

                        {/*но лучше так сделать*/}
                        <React.Suspense fallback={<div><Preloader/></div>}>
                            <Route path='/' exact><Redirect to='/profile'/></Route>
                            <Route path='/profile/:userId?' render={() => <ProfileContainer/>}/>
                            <Route path='/dialogs' render={() => <DialogsContainer/>}/>
                            <Route path='/users' render={() => <UsersContainer/>}/>
                            <Route path='/news' render={() => <News/>}/>
                            <Route path='/music' component={Music}/>
                            <Route path='/gallery' component={Gallery}/>
                            <Route path='/settings' component={Settings}/>
                            <Route path='/login' render={() => <Login/>}/>
                            {/*<Route path="/404" render={() => <div>NotFound404</div>}/>*/}
                            {/*<Route path="*" render={() => <div>NotFound404</div>}/>*/}
                            {/*<Route path='*'><Redirect to='/404'/></Route>*/}
                        </React.Suspense>
                    </Switch>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => ({
    init: state.app.init
});

let AppContainer = compose(
    connect(mapStateToProps, {initApp}),
    withRouter,
)(App);

const MainApp = () => {
    //здесь заменил BrowserRouter на HashRouter только чтобы нормально заработали gh-page, изм-е url в адресной строке
    return (
        <HashRouter> {/*BrowserRouter и Provider используют контекст, чтобы прокинуть store и все настройки внутрь*/}
            <Provider store={store}>
                <AppContainer/>
            </Provider>
        </HashRouter>
    )
};

export default MainApp;
