import React from "react";
import style from './Login.module.css';
import {Field, reduxForm} from "redux-form";
import {Input} from "../common/FormsControls/FormsControls";
import {required} from "../../utils/validators/validators";
import {login} from "../../redux/authReducer";
import {connect} from "react-redux";
import {Redirect} from "react-router-dom";

const LoginForm = ({handleSubmit, error, captchaUrl}) => {
    return (
        <form className={style.form} onSubmit={handleSubmit} action="">
            <h1>Login</h1>
            <Field className={style.login_input} component={Input} type="text" name='login' placeholder='login/E-mail' validate={[required]}/>
            <Field className={style.login_input} component={Input} type="password" name='password' placeholder='password' validate={[required]}/>
            <div className={style.login_rm_input}>
                <Field className={style.login_rm} component={'input'} name='rememberMe' type="checkbox"/> <p>remember me</p>
            </div>
            {captchaUrl && <div>
                <img className={style.login_captcha} src={captchaUrl} alt=""/>
                <Field className={style.login_input} component={Input} type="text" name='captcha' placeholder='enter captcha' validate={[required]}/>
            </div>}
            {/*div покажется если будут ошибки, нужно застилизовать!!!!*/}
            {error && <div>{error}</div>}

            <button className={style.login_button}>log in</button>
        </form>
    )
};

const LoginReduxForm = reduxForm({form: 'login'})(LoginForm);

const Login = props => {
    const onSubmit = (formData) => {
        props.login(formData.login, formData.password, formData.rememberMe, formData.captcha);
    };

    if (props.isAuth) return <Redirect to='/profile'/>;
    return (
        <div>
            <LoginReduxForm onSubmit={onSubmit} captchaUrl={props.captchaUrl}/>
        </div>
    )
};

const mapStateToProps = state => ({
    isAuth: state.auth.isAuth,
    captchaUrl: state.auth.captchaUrl
});

export default connect(mapStateToProps, {login})(Login);