import {auth_API, security_API} from "../api/api";
import {stopSubmit} from "redux-form";

const SET_USER_DATA = 'auth/SET_USER_DATA'; //установить пользовательские данные
const GET_CAPTCHA_URL_SUCCESS = 'auth/GET_CAPTCHA_URL_SUCCESS';
const CLEAR_CAPTCHA = 'auth/CLEAR_CAPTCHA';

export const setAuthUserData = (userId, login, email, isAuth) => ({
    type: SET_USER_DATA,
    payload: {userId, login, email, isAuth}
});

export const getCaptchaUrlSuccess = captchaUrl => ({
    type: GET_CAPTCHA_URL_SUCCESS,
    payload: {captchaUrl}
});
export const clearCaptcha = () => ({
   type: CLEAR_CAPTCHA
});

let initialState = {
    userId: null,
    login: null,
    email: null,
    isAuth: false,
    captchaUrl: null
};

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_DATA:
            return {
                ...state,
                ...action.payload
            };
        case GET_CAPTCHA_URL_SUCCESS:
            return {
                ...state,
                ...action.payload
            };
        case CLEAR_CAPTCHA:
            return {
                ...state.captchaUrl,
                captchaUrl: null

            };
        default:
            return state
    }
};

export const auth = () => async dispatch => {
    let response = await auth_API.auth();
    if (response.resultCode === 0) {
        dispatch(setAuthUserData(response.data.id, response.data.login, response.data.email, true));
    }
};

export const login = (email, password, rememberMe, captcha) => async dispatch => {
    let response = await auth_API.login(email, password, rememberMe, captcha);
    if (response.data.resultCode === 0) {
        dispatch(auth());
    } else {
        if(response.data.resultCode === 10){
            dispatch(getCaptchaUrl())
        }
        let message = response.data.messages.length > 0 ? response.data.messages[0] : "some error";
        dispatch(stopSubmit("login", {_error: message}));
    }
};

export const logout = () => async dispatch => {
    let response = await auth_API.logout();
    if (response.data.resultCode === 0) {
        dispatch(setAuthUserData(null, null, null, false));
        dispatch(clearCaptcha());
    }
};

export const getCaptchaUrl = () => async dispatch => {
    const response = await security_API.getCaptchaUrl();
    dispatch(getCaptchaUrlSuccess(response.data.url))
};

export default authReducer
